import React, { createContext } from 'react';
import { Text } from 'react-native-paper';
import {
  Routes,
  Route
} from 'react-router-native';;

// App components
import Landing from './views/landing';
import DiplomaOfArts from './views/diploma-of-arts';

const AppRoutes = ({ theme, logged_in }) => {
  return (
    <Routes>
      <Route path='/' element={<Landing theme={theme} logged_in={logged_in} />} />
      <Route path='/coming-soon' element={<DiplomaOfArts theme={theme} logged_in={logged_in} />} />
      <Route path='**' element={<Text theme={theme}>There's nothing here!</Text>} />
    </Routes>
  );
}

export default AppRoutes;